/*
 * # modules/grid.css
 * レスポンシブ・gridに関する定義
 */

/*
 * .container
 */
.container {
  -js-display: flex;
  display: flex;
  width: var(--max-width);
  margin: 0 auto;
  /* ie11だと、画像があると隙間ができる */
  min-width: 0%;
}

/*
 * .column-num
 */
[class*='column-'] {
  background-color: #ccc;
}

@for $i from 1 to 12 {
  .column-$i {
    flex: $(i);
  }
}

/*
 * responsive
 */
@media (--sp-viewport) {
  img {
    width: auto;
    max-width: 100%;
    height: auto !important;
  }

  .container {
    /* ie11だと flex-direction: columnで高さが認識されない */
    display: block;
    width: 100%;
  }
}
